  transition: width 0.3s ease;
}

.progress-text {
  font-size: 0.875rem;
  color: #4a5568;
  font-weight: 500;
}

.form-content {
  padding: 2rem 0;
  min-height: 400px;
}

.section-header {
  text-align: center;
