  }

  .questions-list {
    max-width: none;
  }

  .question-item {
    padding: 1rem;
  }
}

/* File Upload Styles */
.file-upload-container {
  position: relative;
}

.file-preview {
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  background: #f8fafc;
}

.file-preview img {
  border-radius: 4px;
}

/* Mentions Input Styles */
.mentions-input-container {
  position: relative;
}

.mentions-suggestions {
  z-index: 1000;
  max-height: 200px;
  overflow-y: auto;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.mentions-suggestions > div {
  padding: 8px 12px;
  cursor: pointer;
  transition: background-color 0.15s;
}

.mentions-suggestions > div:hover {
  background-color: #f3f4f6;
}

.mentions-suggestions > div:not(:last-child) {
  border-bottom: 1px solid #e5e7eb;
}

